import React from 'react';
import styled from 'styled-components';
import { graphql, PageProps } from 'gatsby';
import Card from '@components/Card';
import { color } from '@styles';

const FContentBox = styled.ul`
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding: 0;
`;

const FPostLayout = styled.div`
  width: 100%;
  --deckgo-highlight-code-white-space: pre;
  --deckgo-highlight-code-font-family: 'Consolas';
`;

const FPostFileContent = styled.div`
  width: 100%;
`;

const FPostMarkdownContent = styled.div`
  padding: 1rem;

  h1,
  h2 {
    border-bottom: 1px solid ${color.BORDER};
    padding: 0.75rem 0;
    margin-bottom: 0.75rem;
  }

  h3 {
    padding: 0.5rem 0;
  }

  deckgo-highlight-code,
  p {
    margin-bottom: 1.5rem;
  }

  ul,
  ol {
    list-style-position: inside;
    margin-bottom: 1.5rem;
  }

  h1,
  h2,
  h3,
  h4,
  h5,
  h6,
  p,
  li {
    code {
      border-radius: 0.2rem;
      background-color: ${color.BASE};
      font-size: 0.9rem;
      font-weight: bold;
      font-family: 'GothicA1';
      padding: 0.2rem 0.4rem;
      color: ${color.ACCENT_FG};
    }
  }

  a {
    color: ${color.CLICKABLE_FG};
  }

  blockquote {
    border-left: 0.2rem solid ${color.SECONDARY};
    background-color: ${color.BASE};
    padding-left: 1rem;
  }
`;

interface AboutPageProps {
  file: {
    children: Array<{
      html: string;
    }>;
  } | null;
}

function About({
  data: { file },
  pageContext,
}: PageProps<AboutPageProps, PageContextType>) {
  return (
    <FContentBox>
      <Card maxWidth="33%">
        <img
          style={{
            width: '100%',
            aspectRatio: '1/1',
            padding: '1.25rem',
          }}
          src={pageContext.profileUrl}
        />
      </Card>
      <Card title="소개">
        {file && (
          <FPostLayout>
            <FPostFileContent>
              <FPostMarkdownContent>
                <div
                  style={{
                    padding: '1.25rem',
                  }}
                  dangerouslySetInnerHTML={{
                    __html: file.children[0].html,
                  }}
                />
              </FPostMarkdownContent>
            </FPostFileContent>
          </FPostLayout>
        )}
      </Card>
    </FContentBox>
  );
}

export const query = graphql`
  query {
    file(relativeDirectory: { eq: "" }, ext: { eq: ".md" }) {
      children {
        ... on MarkdownRemark {
          html
        }
      }
    }
  }
`;

export default About;
